import { MathAlphabet } from '../utils/mathAlphabet';

export class Adapter {

  constructor() {
    this.Word = window.Word;
    this.Office = window.Office;
    this.field = {};
    this.alphabet = new MathAlphabet();
  }

  getOptions = async (title) => {
    await this.Word.run(async (context) => {
      if (this.Office.context.document.settings.get(title)) {
        const options = this.Office.context.document.settings.get(title);
        if (options) {
          this.field = { type: options.type, required: options.required, title };
        }
      }

      await context.sync();
    });
  };

  editField = async (dafaultTitle, newField) => {
    await this._findAndReplace(dafaultTitle, newField.title);
    await this._addSetting(newField);
  };

  _findAndReplace = async (dafaultTitle, title) => {
    await this.Word.run(async (context) => {
      const { body } = context.document;
      const searchResultsQuestions = body.search('[{]*[}]', { matchWildCards: true, ignoreSpace: true });
      context.load(searchResultsQuestions);
      await context.sync().then(async () => {
        searchResultsQuestions.items.forEach((item) => {
          const parse = item.text.slice(1, -1);

          const value = this._getMathValue(parse);
          if (dafaultTitle === parse || dafaultTitle === value) {
            item.insertText(`{${title}}`, this.Word.InsertLocation.replace);
          }
        });

        await context.sync();
      });
    });
  };

  _getMathValue(title) {
    console.log(title);
    this.alphabet.build();
    const { length } = title;
    let value = '';
    let i = 0;
    while (i < length) {
      value += this.alphabet.getSymbol(title.codePointAt(i)) ? this.alphabet.getSymbol(title.codePointAt(i)) : '';
      i++;
    }

    return value;
  }

  _addSetting = async (field) => {
    const { type, required, title } = field;
    await this.Word.run(async (context) => {
      const name = title || type.id;
      this.Office.context.document.settings.set(name, { type: type.id, required: required.value });
      this.Office.context.document.settings.saveAsync();

      await context.sync();
    });
  };

}

export default Adapter;



