export class Adapter {

  constructor(field) {
    this.Word = window.Word;
    this.Office = window.Office;
    this.field = field;
  }

  addField = async () => {
    await this._addMarkup();
    await this._addSetting();
  };

  _addMarkup = async () => {
    const { title, type } = this.field;
    const insertString = ` {${title || type.id}} `;
    await this.Word.run(async (context) => {
      let range = context.document.getSelection();
      range.clear();
      range = context.document.getSelection();
      range.insertText(insertString, this.Word.InsertLocation.replace);
      const searchFieldMarkup = range.search('[{]*[}]', { matchWildCards: true, ignoreSpace: true });
      context.load(searchFieldMarkup);

      await context.sync().then(async () => {
        searchFieldMarkup.items[0].font.color = 'blue';
      });
    });
  };

  _addSetting = async () => {
    const { type, required, title } = this.field;
    await this.Word.run(async (context) => {
      const name = title || type.id;
      this.Office.context.document.settings.set(name, { type: type.id, required: required.value });
      this.Office.context.document.settings.saveAsync();

      await context.sync();
    });
  };

}

export default Adapter;



