export class Adapter {

  constructor() {
    this.Word = window.Word;
    this.Office = window.Office;
    this.answers = {};
    this.questions = [];
  }

  getQuestions = async () => {
    await this.Word.run(async (context) => {
      const { body } = context.document;
      const searchResultsQuestions = body.search('[[]*[]]', { matchWildCards: true, ignoreSpace: true });
      context.load(searchResultsQuestions);
      await context.sync().then(async () => {
        searchResultsQuestions.items.forEach((item) => {
          const text = item.text.split(/[|[\]]+/).slice(1, -1)[0];
          const conditions = text.split(';');
          this.getQuestionsFromArray(conditions);
        });

        await context.sync();
      });
    });
  };

  getQuestionsFromArray(conditions) {
    conditions.forEach((q) => {
      const model = {};
      [model.question, model.answer] = q.split(':');

      const hasQuestion = this.questions.includes(model.question);
      if (!hasQuestion) {
        this.questions.push(model.question);
        this.answers[model.question] = [];
        this.answers[model.question].push(model.answer);
      } else {
        const hasAnswer = this.answers[model.question].includes(model.answer);
        if (!hasAnswer) {
          this.answers[model.question].push(model.answer);
        }
      }
    });
  }

  saveQuestion = async (oldQuestion, newQuestion) => {
    await this.Word.run(async (context) => {
      const { body } = context.document;
      const searchResultsQuestions = body.search(`${oldQuestion}:`, { matchWildCards: true, ignoreSpace: true });
      context.load(searchResultsQuestions);

      await context.sync().then(async () => {
        searchResultsQuestions.items.forEach((item) => {
          item.insertText(`${newQuestion}:`, this.Word.InsertLocation.replace);
        });

        await context.sync();
      });
    });
  };

  saveAnswer = async (question, oldAnswer, newAnswer) => {
    await this.Word.run(async (context) => {
      const { body } = context.document;
      const searchResultsQuestions = body
        .search(`${question}:${oldAnswer}`, { matchWildCards: true, ignoreSpace: true });
      context.load(searchResultsQuestions);

      await context.sync().then(async () => {
        searchResultsQuestions.items.forEach((item) => {
          item.insertText(`${question}:${newAnswer}`, this.Word.InsertLocation.replace);
        });

        await context.sync();
      });
    });
  };

}

export default Adapter;



