import React, { Component } from 'react';
import { BrowserRouter } from 'react-router-dom/es';

import { Routes } from './router';
// import { history } from './router/history';

class App extends Component {

  render() {
    return (
      <BrowserRouter
        basename="/doconeaddin"
      >
        {Routes}
      </BrowserRouter>
    );
  }

}

export default App;



